<template lang="html">
  <div class="paramConfig-rule">
    <form-group :form-content="formContent" class="form-group">
      <el-col :span="6">
        <el-button type="success" size="small" @click="addForm" icon="el-icon-plus">新增</el-button>
        <el-button type="primary" size="small" @click="searchForm" icon="el-icon-search">查询</el-button>
        <el-button size="small" @click="clearForm" icon="el-icon-delete">清空</el-button>
      </el-col>
    </form-group>
    <div class="table-wrapper">
      <div class="table-group">
        <el-table ref="mainTable"
                  :data="tableData"
                  v-loading="loadingTable"
                  :max-height="tableHeight"
                  element-loading-text="拼命加载中"
                  border>
                  <el-table-column v-for="(col,index) in tableColumnLabel"
                                  :key="col.label"
                                  :prop="col.label"
                                  :label="col.value"
                                  :width="col.width"
                                  :formatter="col.formatter">
                  </el-table-column>
                  <el-table-column prop="operate"
                                   label="操作"
                                   width="112"
                                   :formatter="formatter">
                                   <template slot-scope="scope">
                                     <el-button v-for='btn in scope.row.operate'
                                               :key='btn'
                                               size="small"
                                               :type="operateBtn(btn,'type')"
                                               @click="handleOperateBtn(scope.$index, scope.row)"
                                               :icon="operateBtn(btn,'icon')"
                                               circle>
                                     </el-button>
                                   </template>
                  </el-table-column>
        </el-table>
      </div>
      <el-pagination class="table-pagination"
                     background
                     layout="prev, pager, next"
                     :page-size="paginationSize"
                     :total="paginationLen"
                     @current-change="handlePagination">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import '@/util/mock'
import {
  fetchPost,
  fetchJson
} from '@/util/fetch'

import {
  mapState
} from 'vuex'
import FormGroup from '@/components/base/formGroup'
import InfoTitle from '@/components/base/moduleInfoTitle'
import MDialog from '@/components/base/dialogModal'

const PAGESIZE = 7
export default {
  components: {
    FormGroup,
    InfoTitle,
    MDialog
  },
  data() {
    return {
      tHeight:'',
      loadingTable: false,
      isShowDialog: false,
      tableColumnLabel: [{
        label: 'rewardName',
        value: '奖励名称'
      }, {
        label: 'rewardType',
        value: '奖励类型'
      }, {
        label: 'actionContent',
        value: '动作内容',
        width: 260
      }, {
        label: 'paramsList',
        value: '参数列表',
        width: 160
      }, {
        label: 'admin',
        value: '最后更新人'
      }, {
        label: 'lastUpdateTime',
        value: '最后更新时间'
      }],
      tableData: [],

      paginationLen: null,
      paginationSize: null
    }

  },
  computed: {
    ...mapState(["rewardsRuleFormLabelAlign", "mainRulesAddModalData"]),

    formContent() {
      return this.rewardsRuleFormLabelAlign
    },
    tableHeight() {
      return document.body.clientHeight - 230
    }
  },
  created() {
    const pageSize = PAGESIZE
    let mainRulesData = fetchJson('/rewardParams-data', {
      pagination: true,
      Size: pageSize,
      Index: 1
    }).then(res => {
      // console.log(Math.floor(res.data.length / pageSize) + 1)
      this.tableData = res.data.tableData
      this.paginationLen = res.data.length
      this.paginationSize = pageSize
    });

    // console.log(mainRulesData.code);
  },
  mounted() {
    this.$nextTick(() => {
      // console.log(this.$refs.mainTable.$el.clientHeight)
    })

  },
  methods: {
    operateBtn(btn, type) {
      let btnType = ''
      let btnIcon = ''
      switch (btn) {
        case 0:
          btnType = 'primary'
          btnIcon = 'el-icon-edit'
          break
        case 1:
          btnType = 'danger'
          btnIcon = 'el-icon-delete'
          break
      }
      if (type === 'icon') {
        return btnIcon
      } else {
        return btnType
      }
    },
    handlePagination(index) {
      console.log(index)
      this.loadingTable = true
      let mainRulesData = fetchJson('/rewardParams-data', {
        pagination: true,
        Size: PAGESIZE,
        Index: index
      }).then(res => {
        this.timer = setTimeout(() => {
          this.loadingTable = false
          this.tableData = res.data.tableData
          clearTimeout(this.timer)
        }, 1200)
      });
    },
    handleOperateBtn(index, row) {
      console.log(index, row)
    },
    searchForm() {
      console.log(this.$store.state.ruleFormLabelAlign)
    },
    clearForm() {
      Object.keys(this.ruleFormLabelAlign).forEach(key => {
        this.ruleFormLabelAlign[key].value = key === 'time' ? [] : ''
      })
      console.log(this.$store.state.ruleFormLabelAlign)
    },
    addForm() {
      this.isShowDialog = true
    },
    handleCloseDialog() {
      this.isShowDialog = false
    },
    handleConfirmDialog() {
      this.isShowDialog = false
    },
    filterBtn(value, row, column) {
      console.log(value, row, column)
    },
    formatter(row, column) {
      return ''
    },
  }
}
</script>
<style media="screen" lang="stylus">
  // .el-table--scrollable-y
  //   .el-table__body-wrapper

</style>
<style lang="stylus" scoped>
.form-group
  padding 10px 20px
  button
    margin-top 34px
.table-wrapper
  position relative
  padding 0 20px
  // max-height calc(100% - 260px)
  // overflow-y auto
  // .table-group
  //   max-height calc(100% - 260px)
  //   overflow auto
  .el-table
    width 99%
  .table-pagination
    position absolute
    left 10px
    margin-top 20px
</style>



